.arrow { width: 65px; height: 65px; background-color: #FFF; cursor: pointer; position: absolute; bottom: 0; font: 0/0 a; text-shadow: none; color: transparent; }
.arrow:before { content: ""; display: block; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 8px; height: 17px; }

.arrow-left { right: 65px; }
.arrow-left:before { background: url(../img/svg/arrow_left.svg); }

.arrow-right { right: 0; }
.arrow-right:before { background: url(../img/svg/arrow_right.svg); }

.popup__wrp { padding: 130px 0 80px; height: 100%; max-width: 1360px; margin: auto; }

.popup__header { height: 50px; padding-left: 50px; position: absolute; top: 0; left: 0; right: 0; }
.popup__header h1 { display: inline-block; padding: 18px 0px 19px; }

.close { position: absolute; top: 0; right: 0; background-color: #c3b091; width: 50px; height: 50px; }
.close:before { content: ""; display: block; background: url(../img/svg/close_button_white.svg) no-repeat center; width: 17px; height: 17px; top: 0; right: 0; bottom: 0; left: 0; margin: auto; }

.d-list { position: relative; font-size: 0; }
.d-list__item { width: 33.3333%; display: inline-block; vertical-align: top; position: relative; padding: 0 25px; }
.d-list__item img { max-width: 347px; width: 100%; display: block; height: auto; margin: auto; }
.d-list__item.is-active { transform: scale(1.1); }
.d-list__item.is-active:after { opacity: 0; }
.d-list__item.is-active .sign { opacity: 1; }
.d-list__item:after { content: ""; display: block; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(255, 255, 255, 0.9); opacity: 1; }

.sign { font-size: 16px; color: #4a4a4a; font-family: "ITCNewVeljovicProCnMd", serif; line-height: 1; text-align: center; padding: 20px 0; opacity: 0; }

.slide-index { display: inline-block; position: absolute; left: 60px; bottom: 16px; font-family: "ITCNewVeljovicProCnMd", serif; font-size: 26px; line-height: 1; color: rgba(74, 74, 74, 0.25); }
.slide-index__current { font-size: 56px; color: #4a4a4a; }


